<template>
  <div>
    <el-form ref="queryInfo" :model="queryInfo" label-width="120px" class="form_border">
      <el-form-item label="" prop="code">
        <el-input v-model="queryInfo.code" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="queryInfo.name" placeholder="" clearable></el-input>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <div class="MT_table_cont">
      <!--  -->
      <div class="btns">
        <el-button @click.stop="add" icon="el-icon-plus"></el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!--  -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="code" label=""> </el-table-column>
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="price" label="()" width="160"> </el-table-column>
        <el-table-column prop="memo" label=""> </el-table-column>
        <!--  -->
        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editBlender(scope.row)"></el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editStorage(scope.row)"></el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editGood(scope.row)"></el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            <el-link v-if="scope.row.state==0" :underline="false" type="primary" @click.stop="enable(scope.row.id)"></el-link>
            <!--    -->
            <!--<el-link v-if="scope.row.shelveStatus === 1" type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row, 1)"><i style="color: #f00"></i></el-link>-->
            <!-- <el-link type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row)"></el-link> -->
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)"></span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)"></span>
        <el-pagination background :total="queryInfo.total" :current-page.sync="queryInfo.page" :page-size.sync="queryInfo.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['tableData', 'queryForm'],
    data() {
      return {
        queryInfo: this.queryForm
      }
    },
    methods: {
      changePage() {
        this.$emit('getPage', this.queryInfo.page)
      },
      search() {
        this.$emit('getSearch', this.queryInfo)
      },
      add() {
        this.$emit('add')
      },
      edit(info) {
        this.$emit('edit', info)
      },
      editBlender(info) {
        this.$emit('editBlender', info)
      },
      editStorage(info) {
        this.$emit('editStorage', info)
      },
      editGood(info) {
        this.$emit('editGood', info)
      },
      del(id) {
        this.$emit('del', id)
      },
      enable(id) {
        this.$emit('enable', id)
      }
    },
    watch: {}
  }
</script>

<style lang="scss" scoped></style>
