<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/role' }"></el-breadcrumb-item>
        <el-breadcrumb-item>Apk</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="apk" prop="filename" ref="apkName">
          <el-upload class="upload-demo" accept=".apk" :limit="1" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeUpload" :on-remove="() => (ruleForm.filename = '')">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> </div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="productName">
          <el-autocomplete class="inline-input" v-model="ruleForm.productName" @blur="mustSel" :fetch-suggestions="querySearch" placeholder="" @select="handleSelect" clearable></el-autocomplete>
        </el-form-item>
        <el-form-item label="" prop="versionName">
          <el-input size="small" v-model="ruleForm.versionName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="versionCode">
          <el-input type="number" size="small" v-model.number="ruleForm.versionCode" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="memo">
          <el-input size="small" v-model="ruleForm.memo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')"></el-button>
          <el-button type="primary" @click="cancelForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// import menuList from '@/utils/menu'
export default {
  data() {
    return {
      ruleForm: {
        filename: '',
        memo: '',
        productId: '', // 
        versionCode: '',
        versionName: '',
        productName: ''
      },
      rules: {
        productName: [{ required: true, message: '', trigger: 'change' }],
        versionName: [{ required: true, message: '', trigger: 'change' }],
        versionCode: [{ required: true, message: '', trigger: 'change' }],
        filename: [{ required: true, message: 'Apk', trigger: 'change' }]
      },
      // 
      restaurants: [],
      isSelect: false,
      productId: '',
      productName: ''
    }
  },

  methods: {
    // 

    uploadAction() {
      return this.$picUpload
      // return ''
    },
    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.filename = res.data.fileName
      let el = this.$refs.apkName.$el.children[1].children[1] // 
      if (el) el.style.display = 'none'
    },
    beforeUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 500
      if (!isLt2M) {
        this.$message({
          message: ' 500MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 
    handleSelect(item) {
      console.log(item)
      this.productId = item.id
      this.isSelect = true // 
    },
    // 
    querySearch(queryString, cb) {
      var params = { /* size: 10, page: 1, */ total: 1, state: 1, name: queryString }
      this.httpGet('/product/query/', params, null, true).then(res => {
        res.rows.map(i => {
          i.value = i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })

      // var results = queryString ? this.restaurants.filter(this.createFilter(queryString)) : this.restaurants
      // //  callback 
      // cb(results)
    },
    // 
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log()
        if (valid) {
          if (!this.isSelect && this.ruleForm.productId !== '') {
            this.ruleForm.productId = ''
            return this.$message.warning('')
          }
          let params = Object.assign({}, this.ruleForm)
          params.productId = this.productId
          console.log(params)
          this.httpPost('/apk/add', params).then(res => {
            this.$message.success('')
            this.$router.back()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    cancelForm() {
      this.$router.back()
    },
    mustSel() {
      this.$mustSel(this, this.ruleForm.productId, 'productName')
    }
  },

  mounted() {}
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
}
</style>
