<template>
  <div>
    <el-form ref="queryForm" :model="queryForm" label-width="100px" class="form_border">
      <el-form-item label="MAC" prop="deviceMac">
        <el-input v-model="queryForm.deviceMac" placeholder="MAC"></el-input>
      </el-form-item>

      <el-form-item label="" prop="productId">
        <!-- <el-select clearable v-model="queryForm.productId" placeholder=""> -->
        <!-- <el-option v-for="option in options" :key="option.id" :label="option.name" :value="option.id"> </el-option> -->
        <!-- </el-select> -->
        <el-autocomplete class="inline-input" v-model="productName" :fetch-suggestions="querySearch" @blur="mustSel(queryForm.productId, 'productName')" placeholder="" @select="handleSelect" clearable></el-autocomplete>
      </el-form-item>

      <el-form-item label="" prop="online">
        <el-select v-model="queryForm.online" placeholder="">
          <el-option label="" value=""> </el-option>
          <el-option label="" value="1"> </el-option>
          <el-option label="" value="0"> </el-option>
        </el-select>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>

    <div class="MT_table_cont">
      <!--  -->
      <div v-if="queryForm.isAll" class="btns">
        <el-button @click.stop="addDevice" icon="el-icon-plus"></el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!--  -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" row-key="id">
        <!-- <el-table-column fixed type="selection" width="50" reserve-selection> </el-table-column> -->
        <el-table-column label="MAC" width="130">
          <template slot-scope="scope">
            <span ref="deviceMac">{{ scope.row.deviceMac }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="name" width="120">
          <template slot-scope="scope">
            {{ scope.row.name ? scope.row.name : '' }}
          </template>
        </el-table-column>
        <el-table-column label="" prop="employeeName" width="80">
          <template slot-scope="scope">
            <el-tag>{{ scope.row.employeeName ? scope.row.employeeName : '' }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="alarm" label="" width="80">
          <template slot-scope="scope">
            <el-link v-if="scope.row.alarm == 0" type="success" :underline="false"></el-link>
            <el-link @click="alarmDevice(scope.row)" v-else type="danger"></el-link>
          </template>
        </el-table-column>
        <el-table-column label="" prop="productName">
          <template slot-scope="scope">
            {{ scope.row.productName ? scope.row.productName : '' }}
          </template>
        </el-table-column>
        <el-table-column prop="deviceAddress" label="">
          <template slot-scope="scope">
            {{ scope.row.deviceAddress ? scope.row.deviceAddress : '' }}
          </template>
        </el-table-column>
        <el-table-column v-if="queryForm.isAll" prop="enterpriseName" label=""></el-table-column>
        <el-table-column prop="sellAmount" label="" width="120"></el-table-column>
        <el-table-column prop="refundAmount" label="" width="120"></el-table-column>
        <el-table-column prop="orderCount" label="" width="120"></el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.online === 1"></el-tag>
            <el-tag type="info" v-else></el-tag>
          </template>
        </el-table-column>
        <!--<el-table-column label="" width="100">-->
        <!--<template slot-scope="scope">-->
        <!--<el-tag v-if="scope.row.enable === 1"></el-tag>-->
        <!--<el-tag type="info" v-else></el-tag>-->
        <!--</template>-->
        <!--</el-table-column>-->
        <!--  -->
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <!--<el-link type="primary" :underline="false" @click="edit(scope.row)"></el-link>-->
            <el-link v-if="!queryForm.isAll" type="primary" :underline="false" @click="editEmployee(scope.row)"></el-link>
            <!-- <el-link v-if="queryForm.isAll" type="primary" :underline="false" @click="editEnterprise(scope.row)"></el-link> -->
            <!--<el-link type="primary" :underline="false" @click="alarmDevice(scope.row)"></el-link>-->
            <!--    -->
            <el-link v-if="queryForm.isAll" type="primary" :underline="false" @click="editDevice(scope.row)"></el-link>
            <el-popover placement="right" width="300" trigger="click">
              <p>{{ scope.row.enable === 1 ? '' : '' }}</p>
              <div>{{ scope.row.plateTitle }}</div>
              <!-- <div style="font-size: 12px;color: rgb(252, 156, 0)">{{ scope.row.cnName }}</div> -->
              <div style="text-align: right; margin: 0">
                <el-button size="mini" type="text" @click="cancelInput(scope.row.id)"></el-button>
                <el-button type="primary" size="mini" @click="putaway(scope.row.enable, scope.row.id)"></el-button>
              </div>
              <el-link v-if="scope.row.enable === 1" type="primary" :underline="false" slot="reference"><i style="color: #f00"></i></el-link>
              <el-link v-else type="primary" :underline="false" slot="reference"></el-link>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="MT_pagination" v-if="queryForm.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryForm, changePage)"></span>
        <span class="jump_last" @click.stop="$jumpToLast(queryForm, changePage)"></span>
        <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
    </div>

    <!-- -->
    <el-dialog title="" :visible.sync="dialogVisible" width="25%">
      <el-divider></el-divider>

      <el-form ref="queryForm" :model="queryForm" label-width="100px">
        <el-form-item label="" prop="enterpriseId">
          <el-autocomplete class="inline-input" v-model="enterpriseName" :fetch-suggestions="queryEnterprise" placeholder="" @blur="mustSel(queryForm.enterpriseId, 'enterpriseName')" @select="enterpriseHandleSelect" clearable></el-autocomplete>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="changeEnterprise"> </el-button>
      </div>
    </el-dialog>

    <!-- -->
    <el-dialog title="" :visible.sync="employeeDialogVisible" width="25%">
      <el-divider></el-divider>

      <el-form ref="queryForm" :model="queryForm" label-width="100px">
        <el-form-item label="" prop="employeeId">
          <el-autocomplete class="inline-input" v-model="employeeName" :fetch-suggestions="queryEmployee" placeholder="" @blur="mustSel(queryForm.employeeId, 'employeeName')" @select="employeeHandleSelect" clearable></el-autocomplete>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="employeeDialogVisible = false"> </el-button>
        <el-button type="primary" @click="changeEmployee"> </el-button>
      </div>
    </el-dialog>

    <!-- -->
    <el-dialog title="" :visible.sync="detailDialog" width="80%">
      <el-divider></el-divider>
      <div v-for="(item, i) in detailData">
        <el-row>
          <el-col :span="2"> {{ i + 1 }}: </el-col>
          <el-col :span="4">
            {{ alarmType(item.alarmType) }}
          </el-col>
          <el-col :span="4">
            {{ item.alarmMemo || '' }}
          </el-col>
          <el-col :span="4">
            {{ item.handleUsername || '' }}
          </el-col>
          <el-col :span="4">
            {{ handleState(item.handleState) }}
          </el-col>
          <el-col :span="4">
            <!-- {{ dateFormat(item.updateDatetime) }} -->
            {{ dateFormat(item.updateDatetime) }}
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['tableData', 'queryForm'],
  data() {
    return {
      queryInfo: this.queryForm,
      multipleSelection: [],
      chooseGoodsStr: '',
      options: [],
      dialogVisible: false,
      // itemName:'',
      formLabelWidth: '100px',

      // 
      restaurants: [],
      // isSelect: false,
      groupSelect: {},
      productName: '',
      selectId: '',
      enterpriseId: '',
      enterpriseName: '',
      employeeId: '',
      employeeName: '',
      employeeDialogVisible: false,
      detailDialog: false,
      detailData: []
    }
  },
  computed: {
    alarmType() {
      return alarmType => {
        return this.$alarmType(alarmType)
      }
    },
    handleState() {
      return handleState => {
        return this.$handleState(handleState)
      }
    },
    dateFormat() {
      const that = this
      return data => {
        return this.$moment(data).format('YYYY-MM-DD HH:MM:SS')
      }
    }
  },
  methods: {
    // 

    // 
    putaway(enable, id) {
      const that = this
      if (enable == 1) {
        console.log(enable, id)
        that.httpPost('/device/stop/' + id, {}, null, true).then(ret => {
          that.$refs.deviceMac.click()
          that.$message.success('')
          that.$emit('getSearch')
        })
      } else {
        that.httpPost('device/start/' + id, {}, null, true).then(ret => {
          that.$refs.deviceMac.click()
          that.$message.success('')
          that.$emit('getSearch')
        })
      }
    },
    // 
    cancelInput(id) {
      console.log(id)
      console.log(this.$refs)
      this.$refs.deviceMac.click()
    },
    searchDetail(id) {
      const params = {
        deviceId: id
      }
      let url = '/device/alarm/query'
      if (this.queryForm.isAll) {
        url = '/device/alarm/query_all'
      }
      this.httpGet(url, params).then(res => {
        this.detailData = res.rows
      })
    },
    // 
    addDevice() {
      this.$router.push({ path: '/editdevice', name: 'EditDevice', params: { type: 'add'} })
    },
    // 
    editDevice(info) {
      // console.log(info)
      this.$router.push({ path: '/editdevice', name: 'EditDevice', params: { type: 'edit', info } })
    },
    //
    editEmployee(row) {
      this.selectId = row.id
      this.employeeId = ''
      this.employeeName = ''
      this.employeeDialogVisible = true
    },
    //
    editEnterprise(row) {
      this.selectId = row.id
      this.enterpriseId = ''
      this.enterpriseName = ''
      this.dialogVisible = true
    },
    alarmDevice(row) {
      this.selectId = row.id
      this.searchDetail(row.id)
      this.detailDialog = true
    },
    handleSelectionChange(val) {
      // this.multipleSelection = val
      // this.chooseGoodsStr = ''
      // let arr = []
      // val.map(item => arr.push(item.id))
      // this.chooseGoodsStr = arr.join(',')
      // console.log(this.chooseGoodsStr)
    },
    handleSelect(item) {
      console.log(item)
      this.groupSelect = item
      this.productName = item.name
      this.queryForm.productId = item.id
      // this.isSelect = true // 
    },
    createFilter(queryString) {
      return restaurant => {
        return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      }
    },

    //
    queryEmployee(queryString, cb) {
      this.associateQuery(queryString, cb, '/basic/employee/query', 'httpPost')
    },

    //
    queryEnterprise(queryString, cb) {
      this.associateQuery(queryString, cb, '/basic/enterprise/query', 'httpPost')
    },

    // 
    querySearch(queryString, cb) {
      this.associateQuery(queryString, cb, '/product/query/', 'httpGet')
    },

    //
    associateQuery(queryString, cb, url, method) {
      var params = { size: 10, page: 1, total: '', state: 1, name: queryString }
      this[method](url, params, null, true).then(res => {
        res.rows.map(i => (i.value = i.name))
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },
    enterpriseHandleSelect(item) {
      console.log(item)
      this.enterpriseName = item.name
      this.enterpriseId = item.id
    },
    employeeHandleSelect(item) {
      console.log(item)
      this.employeeName = item.code + ' - ' + item.name
      this.employeeId = item.id
    },
    mustSel(selId, selName) {
      this.$mustSel(this, selId, selName)
    },
    //
    changeEnterprise() {
      if (!this.enterpriseId) return this.$message.error('')
      var params = {
        deviceId: this.selectId,
        enterpriseId: this.enterpriseId
      }
      this.httpPost('/device/change', params).then(res => {
        this.$message.success('')
        this.dialogVisible = false
        this.search()
      })
    },
    //
    changeEmployee() {
      if (!this.employeeId) return this.$message.error('')
      var params = {
        deviceId: this.selectId,
        employeeId: this.employeeId
      }
      this.httpPost('/device/assign', params).then(res => {
        this.$message.success('')
        this.employeeDialogVisible = false
        this.search()
      })
    },
    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },
    search() {
      if (!this.productName) this.queryForm.productId = ''
      this.$emit('getSearch', this.queryInfo)
    }
  },
  watch: {},
  mounted() {}
}
</script>

<style lang="scss" scoped>
.modal-cont {
  /deep/.el-form-item__content {
    & > div {
      width: 300px !important;
    }
  }
}
/deep/.el-dialog__body {
  .el-row {
    margin-bottom: 20px;
  }
}
</style>
