<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
          <div class="btn_cont">
            <div class="choose_btn _color"></div>
          </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" row-key="id">
          <el-table-column prop="code" label=""> </el-table-column>
          <el-table-column prop="name" label=""> </el-table-column>
          <el-table-column prop="blenderName" label=""> </el-table-column>
          <el-table-column prop="speed" label=""> </el-table-column>
          <el-table-column prop="alarm" label=""> </el-table-column>
          <el-table-column prop="stop" label=""> </el-table-column>
          <el-table-column prop="full" label=""> </el-table-column>
          <el-table-column prop="specialMaterial" label="">
            <template slot-scope="scope">
              <el-tag type="danger" v-if="scope.row.specialMaterial === 0"></el-tag>
              <el-tag v-if="scope.row.specialMaterial === 1"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="memo" label=""> </el-table-column>
          <!--  -->
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <!--<el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>-->
            </template>
          </el-table-column>
        </el-table>
        <div class="fn_btn">
          <el-button v-if="$route.params.type === 'add'" type="primary" @click="cancelForm"></el-button>
          <el-button v-if="$route.params.type === 'add'" type="primary" @click="toProductGood"></el-button>
          <el-button v-if="$route.params.type === 'edit'" type="primary" @click="toProduct"></el-button>
        </div>
      </div>

      <!-- / -->
      <el-dialog :title="title" :visible.sync="dialogVisible" width="80%">
        <el-divider></el-divider>
        <el-form inline ref="ruleForm" :rules="rules" :model="ruleForm" label-width="120px">
          <el-form-item label="" prop="code">
            <el-input disabled size="small" v-model="ruleForm.code"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input size="small" v-model="ruleForm.name" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="blenderId">
            <el-select size="small" v-model="ruleForm.blenderId" placeholder="">
              <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="(g/s)" prop="speed">
            <el-input size="small" v-model="ruleForm.speed" clearable></el-input>
          </el-form-item>
          <el-form-item label="(g)" prop="full">
            <el-input size="small" v-model="ruleForm.full" clearable></el-input>
          </el-form-item>
          <el-form-item label="(g/s)" prop="alarm">
            <el-input size="small" v-model="ruleForm.alarm" clearable></el-input>
          </el-form-item>
          <el-form-item label="(g/s)" prop="stop">
            <el-input size="small" v-model="ruleForm.stop" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="specialMaterial">
            <el-radio-group v-model="ruleForm.specialMaterial">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="memo">
            <el-input style="width: 120%" :rows="2" type="textarea" placeholder="" v-model="ruleForm.memo" maxlength="255"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="resetForm('ruleForm')"> </el-button>
          <el-button type="primary" @click="editBlender('ruleForm')"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        productId: '',
        id: '',
        code: '',
        name: '',
        memo: '',
        blenderId: '',
        speed: '',
        alarm: '',
        full: '',
        stop:'',
        specialMaterial: 0
      },
      options: [],
      tableData: [],
      dialogVisible: false,
      formLabelWidth: '100px',
      title: '',
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        code: [{ required: true, message: '', trigger: 'blur' }],
        blenderId: [{ required: true, message: '', trigger: 'chanage' }],
        speed: [{ required: true, message: '', trigger: 'blur' }],
        alarm: [{ required: true, message: '', trigger: 'blur' }],
        full: [{ required: true, message: '', trigger: 'blur' }],
        stop: [{ required: true, message: '', trigger: 'blur' }],
        specialMaterial: [{ required: true, message: '', trigger: 'change' }]
      }
    }
  },
  methods: {
    cancelForm() {
      this.$router.push({
        path: '/blender',
        name: 'Blender',
        params: { type: 'add', productId: this.ruleForm.productId, info: this.$route.params.info }
      })
    },
    toProduct() {
      this.$router.push({
        path: '/product'
      })
    },
    toProductGood() {
      this.$router.push({
        path: '/productGood',
        name: 'ProductGood',
        params: { type: 'add', productId: this.ruleForm.productId, info: this.$route.params.info }
      })
    },
    loadData() {
      this.httpGet('/product/storage/query/' + this.ruleForm.productId, '').then(res => {
        this.tableData = res
      })
    },
    blenderQuery() {
      this.httpGet('/product/blender/query/' + this.ruleForm.productId, '').then(res => {
        this.options = res
      })
    },
    add() {
      this.title = ''
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm = {
          productId: this.$route.params.productId,
          code: this.$route.params.info.code.concat(this.$formatCode(this.tableData.length)),
          id: '',
          name: '',
          memo: '',
          blenderId: '',
          speed: '',
          alarm: '',
          full: '',
          specialMaterial: 0
        }
      })
    },

    edit(info) {
      this.title = ''
      this.ruleForm = Object.assign({}, info)
      this.dialogVisible = true
    },

    editBlender(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.title == '') {
            this.httpPost('/product/storage/add', this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '!' })
              this.dialogVisible = false
              this.loadData()
            })
          } else {
            this.httpPost('/product/storage/mod/' + this.ruleForm.id, this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '!' })
              this.dialogVisible = false
              this.loadData()
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.dialogVisible = false
    }
  },

  mounted() {
    this.ruleForm.productId = this.$route.params.productId
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else if (this.$route.params.type === 'edit') {
      this.type = 'edit'
    } else {
      this.$router.push({
        path: '/product'
      })
      return
    }
    this.blenderQuery()
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  & > div {
    width: 240px !important;
  }
}
</style>
