<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/stockbin' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="" prop="code">
          <el-input :disabled="$route.params.type === 'edit'" size="small" style="width: 300px" v-model="ruleForm.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input :disabled="$route.params.type === 'edit'" size="small" style="width: 300px" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item v-if="ruleForm.specialMaterial === '0'" label="g/s" prop="discharging">
          <el-input  size="small" style="width: 300px" v-model="ruleForm.discharging" @input="ruleForm.discharging = ruleForm.discharging.replace(/[^\d.]/g, '')"></el-input>
        </el-form-item>
        <el-form-item label="" prop="alarm">
          <el-input size="small" style="width: 300px" v-model="ruleForm.alarm"></el-input>
        </el-form-item>
        <el-form-item label="" prop="full">
          <el-input size="small" style="width: 300px" v-model="ruleForm.full"></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <el-radio-group v-model="ruleForm.specialMaterial">
            <el-radio label="1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')"></el-button>
          <el-button type="primary" @click="cancelForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        code: '',
        name: '',
        discharging: '',
        alarm: '',
        full: '',
        specialMaterial: '0'
      },
      rules: {
        code: [{ required: true, message: '', trigger: 'blur' }],
        name: [{ required: true, message: '', trigger: 'blur' }],
        alarm: [{ required: true, message: '', trigger: 'blur' }],
        full: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },

  methods: {
    // 
    saveForm(formName) {
      if(this.ruleForm.specialMaterial==0&&!this.ruleForm.discharging){
        this.$message.error('')
      }
      if(this.ruleForm.specialMaterial==1)
        this.ruleForm.discharging=''
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm);
          if(this.type =='add'){
            this.httpPost("/silo/add",params).then(res => {
              this.$message.success('')
              this.$router.push({
                path: '/stock',
                query: {}
              })
            })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
          else{
            this.httpPost("/silo/mod",params).then(res => {
              this.$message.success(''),
                this.$router.push({
                  path: '/stock',
                  query: {}
                })
            })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.type = 'edit'
      this.ruleForm = params.info
      this.ruleForm.specialMaterial = params.info.specialMaterial.toString()
    }else{
      this.type = 'add'
    }
  }
}
</script>

<style lang="scss" scoped>
.hidden {
  visibility: hidden;
}
.row_item {
  display: flex;
  flex-wrap: wrap;
  /deep/.el-form-item__content {
    width: 100px;
  }
}
.move_btn {
  margin-left: 50px;
  img {
    width: 20px;
    height: 20px;
    padding: 4px;
    border: 1px dashed #333;
    border-radius: 5px;
    margin: 0 5px;
    cursor: pointer;
  }
}
.stock_cont {
  /deep/.el-form-item__content {
    padding-top: 2px;
  }
}
</style>
