<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="120px" class="form_border">
        <el-form-item label="" prop="name">
          <el-input v-model="queryForm.name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item class="select_btn">
          <el-button type="primary" @click="search"></el-button>
        </el-form-item>
      </el-form>
      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus"></el-button>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="code" label=""> </el-table-column>
          <el-table-column prop="name" label=""> </el-table-column>
          <el-table-column prop="discharging" label="(g/s)">
            <template slot-scope="scope">
              {{ scope.row.discharging ? scope.row.discharging : '' }}
            </template>
          </el-table-column>
          <el-table-column prop="alarm" label=""> </el-table-column>
          <el-table-column prop="full" label=""> </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.specialMaterial"></el-tag>
              <el-tag type="info" v-else></el-tag>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>

      <!--  -->
      <!--<el-dialog title="" :visible.sync="dialogVisible" width="30%">-->
      <!--<el-divider></el-divider>-->
      <!--<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">-->
      <!--<el-form-item label="" prop="discharging">-->
      <!--<el-input size="small" style="width: 300px" v-model="ruleForm.discharging"></el-input>-->
      <!--</el-form-item>-->
      <!--</el-form>-->
      <!--<div slot="footer" class="dialog-footer">-->
      <!--<el-button @click="dialogVisible = false"> </el-button>-->
      <!--<el-button type="primary" @click="saveForm('ruleForm')"> </el-button>-->
      <!--</div>-->
      <!--</el-dialog>-->
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        name: '',
        plateVal: '',
        size: 10,
        page: 1,
        total: ''
      },

      tableData: [],
      options: [],
      dialogVisible: false,

      formLabelWidth: '100px',
      ruleForm: {
        discharging: ''
      }
      // rules: {
      //   discharging: [{ required: true, message: '', trigger: 'blur' }]
      // }
    }
  },
  methods: {
    // 
    search() {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 
    add() {
      this.$router.push({ path: '/editStock', name: 'EditStock', params: { type: 'add' } })
    },

    // 
    edit(info) {
      this.$router.push({ path: '/editStock', name: 'EditStock', params: { type: 'edit', info } })
    },

    editdischarging() {
      this.dialogVisible = true
    },
    // 
    // saveForm(formName) {
    //   this.$refs[formName].validate(valid => {
    //     if (valid) {
    //       console.log(this.ruleForm)
    //     } else {
    //       console.log('error submit!!')
    //       return false
    //     }
    //   })
    // },

    loadData() {
      this.httpGet('/silo/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 240px !important;
  }
}
</style>
