<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/staff' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form inline :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="ruleForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="account">
          <el-input size="small" v-model="ruleForm.account" clearable></el-input>
        </el-form-item>
        <el-form-item v-if="type === 'add'" label="" prop="password">
          <el-input type="password" size="small" v-model="ruleForm.password" clearable></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="enterpriseName">
          <el-autocomplete class="inline-input" v-model="ruleForm.enterpriseName" :fetch-suggestions="querySearch" placeholder="" @select="handleSelect" clearable></el-autocomplete>
        </el-form-item> -->
        <el-form-item label="" prop="code">
          <el-input size="small" v-model="ruleForm.code" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input size="small" v-model="ruleForm.mobile" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="ruleForm.email" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-date-picker v-model="ruleForm.birthday" type="date" placeholder="" value-format="yyyy-MM-dd"> </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-select v-model="ruleForm.gender" placeholder="">
            <el-option v-for="item in genderOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="memo">
          <el-input size="small" v-model="ruleForm.memo" clearable></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox-group v-model="ruleForm.roleList">
            <el-checkbox :label="item.id" v-for="(item, index) in roleList" :key="item.id" :value="item.id">{{ item.name }}</el-checkbox>
          </el-checkbox-group>
          <!-- <div v-else></div> -->
        </el-form-item>
        <el-form-item label=" ">
          <el-button type="primary" @click="saveForm('ruleForm')"></el-button>
          <el-button type="primary" @click="cancelForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        id: '',
        name: '',
        code: '',
        account: '',
        password: '',
        mobile: '',
        email: '',
        gender: '',
        birthday: '',
        memo: '',
        enterpriseId: '',
        enterpriseName: '',
        roleList: []
      },
      restaurants: [],
      roleList: [],
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        account: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        enterpriseName: [{ required: true, message: '', trigger: 'change' }]
      },
      genderOptions: [
        { value: '0', label: '' },
        { value: '1', label: '' },
        { value: '2', label: '' },
        { value: '5', label: '' },
        { value: '6', label: '' },
        { value: '9', label: '' }
      ],
      // showRole: false,
      type: ''
    }
  },

  methods: {
    queryRole() {
      var params = {
        // enterpriseId: enterpriseId,
        state: 1, //1:0:
        size: 99,
        page: 1
      }
      this.httpPost('/basic/role/query', params).then(res => {
        this.roleList = res.rows
        console.log(this.roleList)
      })
    },
    mustSel() {
      if (!this.ruleForm.enterpriseId) this.ruleForm.enterpriseName = null
    },
    handleSelect(item) {
      console.log(item)
      // this.groupSelect = item
      // this.deviceCategoryId = item.id
      this.ruleForm.enterpriseId = item.id
      this.ruleForm.enterpriseName = item.name
      this.mustSel()
      this.queryRole(item.id)
    },
    //
    querySearch(queryString, cb) {
      var queryForm = {
        size: 10,
        page: 1,
        total: '',
        name: queryString,
        state: 1
      }
      this.httpPost('/basic/enterprise/query', queryForm, null, true).then(res => {
        res.rows.map(i => {
          i.value = i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },

    // 
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm)
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          console.log(params)
          if (this.type == 'add') {
            this.httpPost('/basic/employee/add', params).then(res => {
              this.$message.success('')
              this.$router.push({
                path: '/staff',
                query: {}
              })
            })
          } else {
            // debugger
            params.password = ''
            this.httpPost('/basic/employee/mod/' + params.id, params).then(res => {
              this.$message.success(''),
                this.$router.push({
                  path: '/staff',
                  query: {}
                })
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    chooseResource() {
      this.$router.push({ path: '/editResource', name: 'EditResource', params: { type: 'add', info: this.ruleForm } })
    },
    // 
    cancelForm() {
      this.$router.back()
    }
  },
  watch: {
    'ruleForm.enterpriseName'(newVal, oldVal) {
      console.log(newVal)
      if (newVal) {
        // this.showRole = true
      } else {
        // this.showRole = false
        this.ruleForm.roleList = []
      }
    }
  },
  mounted() {
    let type = this.$getData('staffType')
    if (type === 'add') {
      this.type = 'add'
    } else {
      this.type = 'edit'
      this.ruleForm = this.$route.params.info
      this.ruleForm.gender = this.ruleForm.gender && this.ruleForm.gender.toString()
      this.ruleForm.birthday = this.ruleForm.birthday && new Date(this.ruleForm.birthday)
      this.httpPost('/basic/employee/query/role/' + this.$route.params.info.id, '').then(res => {
        this.ruleForm.roleList = res
      })
    }
    this.queryRole()
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
}
</style>
